label{
	color: white;
	font-size: 35px;
	line-height: 60px;
	padding: 0 100px;
	font-weight: bold;
}
nav ul{
	float: right;
	margin-right: 20px;
}
nav ul li{
	display: inline-block;
	line-height: 60px;
	margin: 0 5px;
}
nav ul li #a{
	color: white;
	font-size: 17px;
	padding: 7px 13px;
	border-radius: 3px;
	text-transform: uppercase;
}
#a:hover{
	background: #1b9bff;
	transition: .5s;
}
.checkbtn,.checkcart{
	font-size: 30px;
	color: white;
	float: right;
	line-height: 60px;
	margin-right: 40px;
	cursor: pointer;
	display: none;
}
.checkcart{
	float: left;
}
#check{
	display: none;
}

@media (max-width: 800px){
	label{
		font-size: 30px;
		padding-left: 50px;
	}
	nav ul li #a{
		font-size: 16px;
	}	
}

@media (max-width: 760px){
		.checkbtn,.checkcart{
			display: block;	
		}
		.checkcart{
			margin: 0;
			left: 0;
		}
		nav label{
			font-size: 30px;
			padding-left: 50px;
			padding-right: 10px;
		}
		nav ul{
			position: fixed;
			width: 100%;
			height:100vh;
			background: #2c3e50;
			top: 80px;
			left: -100%;
			text-align: center;
			transition: all .5s;
		}
		nav ul li{
			display: block;
		}
		nav ul li #a{
			font-size: 20px;
		}
		#a:hover{
			background: none;
			color: #0082e6
		}
		#check:checked ~ ul{
			left: 0;
	}
}

	